.container {
    display: -ms-flexbox;
    display: flex;
    min-height: 80%;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    -ms-flex-pack: center;
    justify-content: center;
    padding-top: 50px;
    padding-bottom: 50px;
    width: 90%;
    margin: 0 auto
}

@media (min-width: 1100px) {
    .container {
        padding-left:100px;
        padding-right: 100px;
        width: 1100px
    }
}


